
  .home {
    width: 100%;
    height: 100vh;
    position: relative;
    object-fit: cover;
    background-color: rgb(100, 89, 89);
    background-image: url(./../images/firstB2.png);
    background-size: cover;
    // top: -40px;
    background: cover;

    .menu1 {
      position: fixed;
      top: 0;
      // -webkit-backdrop-filter: blur(20px);
      // backdrop-filter: blur(20px);
      width: 100%;
      height: 70px;
      background-color: chocolate;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      .logo {
        border-radius: 50%;
        width: 70px;
        height: 70px;
        border-radius: 50%;
        background-color: aqua;

        .logo_img {
          border-radius: 50%;
          width: 70px;
          height: 70px;
          object-fit: cover;
        }
      }

      .cover_menu1 {
        align-items: center;
        margin: auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 85%;

        .write1_menu1 {
          align-items: center;
          display: flex;
          flex-wrap: wrap;
          font-size: 20px;

          .writeM1 {
            padding: 20px;
            align-items: center;
            color: rgb(255, 255, 255);

          }
        }

        .write2_menu1 {
          align-items: center;
          display: flex;
          flex-wrap: wrap;
          font-size: 20px;

          .writeM2 {
            padding: 20px;
            align-items: center;
            color: rgb(255, 255, 255);

          }
        }
      }
    }

    .cover_me {
      width: 80%;
      margin: auto;
      padding-top: 200px;

      .image_me {
        
        align-items: center;
        margin: auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 260px;
        height: 260px;
        // background-color: rgb(97, 92, 50);
        border-radius: 50% 50% 50% 50%;
        border: solid rgb(255, 255, 255) 10px;

        .image_me_url {
          margin: auto;
          border-radius: 50%;
          width: 230px;
          max-height: 250px;

        }
      }

      .write_me1 {
        text-align: center;
        font-size: 30px;
        color: white;
      }
    }
  }
